import { Injectable } from "@angular/core";
//import { CryptoJS } from "../lib/assets/js/crypto-js/aes";
declare var CryptoJS: any;

@Injectable({
  providedIn: "root"
})
export class EauthService {
  constructor() {}

  encrypt(str) {
    let key = CryptoJS.enc.Utf8.parse("elprosystem");
    let iv = CryptoJS.enc.Utf8.parse("elprosystem");
    let encrypted = CryptoJS.AES.encrypt(str, key, {
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7
    });
    return encrypted.toString();
  }

  //id: wubwub-14-west:e389d450-4d3f-4949-a089-c7f0341a2e65
}
